            text
            color="primary"
            v-if="!project.userPendingEvaluationsCount > 0"
            @click="openProject"
          >
            <v-icon>mdi-plus</v-icon>
          </v-btn>
          <v-btn
            rounded
            elevation="0"
            color="white"
            class="primary--text text-sm px-8 mt-1"
            small
            v-else
            @click.stop="evaluateProject"
          >
            <!-- <v-icon>mdi-check</v-icon> -->
            {{ $t('Evaluate') }}
          </v-btn>
        </div>
      </v-card-text>
      <v-divider class="mt-3" />
      <v-card-actions v-if="!project.userPendingEvaluationsCount > 0">
        <v-row>
          <v-col cols="6" class="pr-0 py-0">
            <v-btn block text color="primary" @click="showIdeaForm">
              <v-icon>mdi-lightbulb-outline</v-icon>
              <span class="text">{{ $t('Share idea') }}</span>
            </v-btn>
          </v-col>
          <v-col cols="6" class="pl-0 py-0">
            <v-btn block text color="danger" @click="showIssueForm">
              <v-icon>mdi-chat-outline</v-icon>
              <span class="text">{{ $t('Report issue') }}</span>
            </v-btn>
          </v-col>
        </v-row>
      </v-card-actions>
      <v-card-actions v-else class="center">
        <v-row>
          <v-col cols="12" class="pl-0 py-0">
            <v-btn block text color="danger" @click="evaluateProject(project)">
              <span
                class="text"
              >{{ $tc("pendingEvaluation.count", project.userPendingEvaluationsCount) }}</span>
            </v-btn>
          </v-col>
        </v-row>
      </v-card-actions>
    </v-card>
    <v-dialog
      v-model="isIdeaFormShowing"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <div class="d-overlay"></div>
      <idea-form
        @close="closeIdeaForm"
        :idea="currentIdea"
        :project="project"
        mode="idea"
        :key="intent"
      />
    </v-dialog>

    <v-dialog
      v-model="isIssueFormShowing"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <div class="d-overlay"></div>
      <issue-form @close="closeIssueForm" :issue="currentIssue" :project="project" :key="intent" />
    </v-dialog>
    <!-- 
    <v-dialog
      v-model="isShowingProjectDetails"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <div class="d-overlay"></div>
      <project-details :project="currentProject" @close="closeProject"></project-details>
    </v-dialog>

    <v-dialog
      v-model="isShowingProjectEvaluation"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <div class="d-overlay"></div>
      <project-evaluate
        :project="currentEvaluatingProject"
        @close="closeProject"
        :key="'evaluate_' + (new Date()).getUTCMilliseconds()"
      ></project-evaluate>
    </v-dialog>-->
  </div>
</template>
<script>
import IdeaForm from "./idea/Form";
import IssueForm from "./issue/Form";
// import ProjectDetails from "./Details";
// import ProjectEvaluate from "./evaluate/Index";
//import { Idea, Issue } from "@/models";
export default {
  props: {
    project: {
      required: true,
      type: Object
    }
  },
  components: {
    "idea-form": IdeaForm,
    "issue-form": IssueForm
    // "project-details": ProjectDetails,
    // "project-evaluate": ProjectEvaluate
  },
  data: () => ({
    filter: {},
    isIssueFormShowing: false,
    isIdeaFormShowing: false,
    isShowingProjectDetails: false,
    isShowingProjectEvaluation: false,
    currentEvaluatingProject: null,
    currentIdea: null,
    currentIssue: null,
    currentProject: null,
    intent: new Date().getUTCMilliseconds()
  }),
  methods: {
    showIssueForm() {
      // this.currentIssue = new Issue();
      // this.currentIssue.processId = this.project.processId;
      // this.intent = new Date().getUTCMilliseconds();
      // this.isIssueFormShowing = true;
      this.$router.push({
        name: "project-report-issue",
        params: {
          id: this.project.id
        }
      });
    },
    closeIssueForm() {
      this.isIssueFormShowing = false;
    },
    openProject() {
      // this.currentProject = project;
      // this.isShowingProjectDetails = true;
      this.$router.push({
        name: "project-ideas",
        params: {
          id: this.project.id,
          tab: "stage"
        }
      });
    },
    evaluateProject() {
      // this.currentEvaluatingProject = project;
      // this.isShowingProjectEvaluation = true;
      this.$router.push({
        name: "project-evaluate",
        params: {
          id: this.project.id
        }
      });
    },
    closeProject() {
      this.isShowingProjectDetails = false;
      this.isShowingProjectEvaluation = false;
    },
    showIdeaForm() {
      // this.currentIdea = new Idea();
      // this.currentIdea.processId = this.project.processId;
      // this.intent = new Date().getUTCMilliseconds();
      // this.isIdeaFormShowing = true;
      this.$router.push({
        name: "project-share-idea",
        params: {
          id: this.project.id
        }
      });
    },
    closeIdeaForm() {
      this.isIdeaFormShowing = false;
    }
  }
};
</script>