<template>
  <v-card
    class="d-flex flex-column project-detail"
    :class="{'has-expanded-content':this.currentIdeaId }"
  >
    <v-toolbar color="primary" elevation="1" height="56">
      <v-toolbar-title class="card-header">
        <h4 class="title white--text">{{ $t('Ideas') }}</h4>
        <!-- <small class="subtitle white--text">{{ project.name }}</small> -->
        <div class="right-tools">
          <v-btn class="white--text" icon @click="close">
            <v-icon>mdi-minus</v-icon>
          </v-btn>
        </div>
      </v-toolbar-title>
    </v-toolbar>
    <div style class="bg-light flex-grow-1">
      <v-card-text v-if="currentProject && currentProject.loaded">
        <v-tabs v-model="tab" background-color="white mb-1" grow class="flex-grow-0" height="56">
          <v-tab v-for="tab in tabs" :key="tab">
            {{ $t(tab) }}
            <v-badge class="badge" inline :content="countIdeas(tab.toLowerCase())" v-if="countIdeas(tab.toLowerCase()) > 0"></v-badge>
          </v-tab>
        </v-tabs>
        <v-tabs-items
          v-model="tab"
          class="flex-grow-1 d-flex flex-column bg-light no-transition"
          style="overflow:auto;height:calc(100vh - 147px - var(--vh-offset, 0px));"
          ref="tabsContainer"
        >
          <v-tab-item key="stage" class="pt-3 px-3 bg-light flex-grow-1 content" ref="stageContent">
            <pull-to-refresh
              :refresh-fn="()=>this.loadProject(true)"
              :disabled="this.currentIdeaId"
            >
              <div v-if="countIdeas('stage') > 0">
                <v-lazy
                  v-for="idea in stageIdeas"
                  :key="idea.id"
                  :options="{
                threshold: .5
              }"
                  min-height="50"
                  transition="fade-transition"
                >
                  <idea-card
                    :idea="idea"
                    :project="project"
                    :expanded="currentIdeaId===idea.id"
                    :ref="'idea_' + idea.id"
                    @toggle="toggleIdea"
                  ></idea-card>
                </v-lazy>
              </div>
              <div v-else>
                <empty-data :text="$t('Looks empty! Ideas?')"></empty-data>
              </div>
            </pull-to-refresh>
          </v-tab-item>
          <v-tab-item key="operation" class="pt-3 px-3 bg-light flex-grow-1 content">
            <pull-to-refresh
              :refresh-fn="()=>this.loadProject(true)"
              :disabled="this.currentIdeaId"
            >
              <div v-if="countIdeas('operation') > 0">
                <v-lazy
                  v-for="idea in operationIdeas"
                  :key="idea.id"
                  :options="{
                threshold: .5
              }"
                  min-height="50"
                  transition="fade-transition"
                >
                  <idea-card
                    :idea="idea"
                    :project="project"
                    :expanded="currentIdeaId===idea.id"
                    :ref="'idea_' + idea.id"
                    @toggle="toggleIdea"
                  ></idea-card>
                </v-lazy>
              </div>
              <div v-else>
                <empty-data :text="$t('Looks empty! Ideas?')"></empty-data>
              </div>
            </pull-to-refresh>
          </v-tab-item>
          <v-tab-item key="phase" class="pt-3 px-3 bg-light flex-grow-1 content">
            <pull-to-refresh
              :refresh-fn="()=>this.loadProject(true)"
              :disabled="this.currentIdeaId"
            >
              <div v-if="countIdeas('phase') > 0">
                <v-lazy
                  v-for="idea in phaseIdeas"
                  :key="idea.id"
                  :options="{
                threshold: .5
              }"
                  min-height="50"
                  transition="fade-transition"
                >
                  <idea-card
                    :idea="idea"
                    :project="project"
                    :expanded="currentIdeaId===idea.id"
                    :ref="'idea_' + idea.id"
                    @toggle="toggleIdea"
                  ></idea-card>
                </v-lazy>
              </div>
              <div v-else>
                <empty-data :text="$t('Looks empty! Ideas?')"></empty-data>
              </div>
            </pull-to-refresh>
          </v-tab-item>
        </v-tabs-items>
      </v-card-text>
      <v-card-text v-else style="height:100%">
        <page-loader></page-loader>
      </v-card-text>
    </div>
  </v-card>
</template>
<script>
import IdeaCard from "./idea/Card";
//import { mapGetters } from "vuex";
export default {
  props: {
    project: {
      type: Object,
      required: true
    }
  },
  components: {
    "idea-card": IdeaCard
  },
  data: () => ({
    currentProject: null,
    tab: null,
    tabs: ["Stage", "Operation", "Phase"],
    currentIdeaId: null
  }),
  computed: {
    stageIdeas: {
      get: function() {
        if (!this.currentProject) {
          return [];
        }
        return this.currentProject.userIdeas.filter(
          o => o.idea.parent.__typename === "ProcessStage"
        );
      }
    },
    operationIdeas: {
      get: function() {
        if (!this.currentProject) {
          return [];
        }
        return this.currentProject.userIdeas.filter(
          o => o.idea.parent.__typename === "ProcessOperation"
        );
      }
    },
    phaseIdeas: {
      get: function() {
        if (!this.currentProject) {
          return [];
        }
        return this.currentProject.userIdeas.filter(
          o => o.idea.parent.__typename === "ProcessPhase"
        );
      }
    }
  },
  async mounted() {
    // this.tab = "Stage";
    await this.loadProject();
  },
  methods: {
    close() {
      this.$emit("close");
    },
    toggleIdea(idea) {
      if (this.currentIdeaId === idea.id) {
        this.currentIdeaId = null;
      } else {
        this.currentIdeaId = idea.id;
      }
      if (this.currentIdeaId) {
        this.$nextTick(() => {
          this.$refs.stageContent.scrollTop = 0;
        });
      }
    },
    ideasBySection(section) {
      return this[section.toLowerCase() + "Ideas"];
    },
    countIdeas(section) {
      return this.ideasBySection(section.toLowerCase()).length;
    },
    async loadProject(force = false) {
      this.currentProject = await this.$store.dispatch("project/findById", {
        id: this.project.id,
        force: force
      });
    }
  }
};
</script>