<template>
  <div class="stepper-step animated faster" :class="slideDirection">
    <div class="header">
      <div class="sub-title" style="font-size:.625rem;color:#666">{{ project.process.title }}</div>
      <div class="title">{{ project.name }}</div>
      <!-- <div class="check">
        <i class="mdi mdi-check"></i>
      </div>-->
    </div>
    <div class="content d-flex flex-column" style="padding:.5rem 1rem" @keyup="validate">
      <div class="flex-grow-0">
        <v-text-field
          v-if="!form.sourceId"
          v-validate="'required'"
          v-model="form.title"
          color="danger"
          name="title"
          ref="title"
          :label="$t('Title') + ' (' + $t('Mandatory') +')'"
        />
      </div>
      <div class="flex-grow-1">
        <v-textarea
          height="100%"
          color="danger"
          v-validate="'required'"
          class="h-100 textarea"
          name="description"
          no-resize
          :label="$t('Issue description') + ' (' + $t('Mandatory') +')'"
          v-model="form.description"
          counter
        />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  title: "Describe your issue",
  props: {
    project: {
      required: true,
      type: Object
    },
    issue: {
      required: true,
      type: Object
    },
    form: {
      required: true,
      type: Object
    },
    slideDirection: {
      required: false
    }
  },
  mounted() {
    this.validate();
    setTimeout(() => {
      this.$refs.title.focus();
    }, 500);
    setTimeout(() => {
      this.validate();
    });
  },
  methods: {
    validate() {
      setTimeout(() => {
        this.$validator.validateAll();
        this.$emit("validate", !this.vErrors.any());
      }, 100);
    }
  }
};
</script>