<template>
  <div class="home container animated h-100 slideInLeft faster d-flex flex-column">
    <pull-to-refresh :refresh-fn="()=>loadProjects(true)">
      <div v-if="((!filter.busy))">
        <div v-if="items.length>0">
          <v-lazy
            v-for="item in items"
            :key="item.id"
            :options="{
                threshold: .5
              }"
            min-height="100"
            transition="fade-transition"
          >
            <project-card :project="item"></project-card>
          </v-lazy>
        </div>
        <empty-data v-else :text="$t('You don\'t have active projects!')"></empty-data>
      </div>
      <page-loader v-else></page-loader>
    </pull-to-refresh>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import Card from "./Card";
export default {
  components: {
    "project-card": Card
  },
  data: () => ({
    filter: {
      busy: false,
      force: true
    }
  }),
  computed: {
    ...mapGetters({
      projects: "project/filteredItems"
    }),
    items: {
      get: function() {
        const projects = [...this.projects];
        projects.sort((a, b) => {
          if (a.hasUserPendingEvaluations && !b.hasUserPendingEvaluations) {
            return -1;
          }
          if (b.hasUserPendingEvaluations && !a.hasUserPendingEvaluations) {
            return 1;
          }
          return a.name > b.name ? 1 : -1;
        });
        return projects;
      }
    }
  },
  mounted() {
    this.loadProjects();
  },
  methods: {
    async loadProjects(force = false) {
      await this.$store.dispatch("project/findAll", {
        filter: this.filter,
        force: force
      });
    }
  }
};
</script>