<template>
  <div class="stepper-step">
    <div class="header">
      <div class="sub-title" style="font-size:.625rem">{{ project.process.title }}</div>
      <div class="title">{{ project.name }}</div>
      <!-- <div class="check" :class="{'danger':this.item.type==='PROBLEM'}">
        <i class="mdi mdi-check"></i>
      </div>-->
    </div>
    <div class="content d-flex flex-column" style="padding:.5rem 1rem" @keyup="validate">
      <div class="flex-grow-1">
        <v-textarea
          height="100%"
          v-validate="'required'"
          class="h-100 textarea"
          name="description"
          no-resize
          :color="this.item.type==='PROBLEM'?'danger':'primary'"
          :label="$t('Description') + ' (' + $t('Mandatory') +')'"
          v-model="form.description"
          counter
          autofocus
        />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  title: "Description",
  props: {
    project: {
      required: true,
      type: Object
    },
    item: {
      required: true,
      type: Object
    },
    form: {
      required: true,
      type: Object
    }
  },
  mounted() {
    this.validate();
    setTimeout(() => {
      this.validate();
    });
  },
  methods: {
    validate() {
      setTimeout(() => {
        this.$validator.validateAll();
        this.$emit("validate", !this.vErrors.any());
      }, 100);
    }
  }
};
</script>