<template>
  <div>
    <v-card class="idea-card" :class="{'expanded':expanded}">
      <v-card-title class="header" @click="toggle">
        <small class="supertitle">{{ idea.idea.parent.title }}</small>
        <h3 class="title">{{ idea.idea.title }}</h3>
        <div class="actions">
          <div v-if="idea.idea.hasFile">
            <v-icon class="file" style="font-size:1rem;margin:4px 0">mdi-folder-open-outline</v-icon>
          </div>
          <v-btn class="expand" icon>
            <span class="icon"></span>
          </v-btn>
        </div>
      </v-card-title>
      <v-card-text v-if="expanded" class="detail">
        <!-- <span class="date">{{ idea.idea.createdAt }}</span> -->
        <p class="description">{{ idea.idea.description}}</p>
        <div class="actions">
          <div v-if="idea.idea.hasFile">
            <v-btn
              :href="idea.idea.file.url"
              color="secondary"
              block
              rounded
              outlined
              target="_blank"
              class="no-text-transform mb-3"
              large
            >
              <v-icon class="file" style="font-size:1.2rem;margin-right:5px">mdi-folder-open-outline</v-icon>
              &nbsp;{{ $t('Download guide') }}
            </v-btn>
          </div>
          <v-btn
            color="primary"
            class="no-text-transform mb-3"
            block
            outlined
            large
            @click="showImprovementForm('IMPROVEMENT')"
          >
            <v-icon>mdi-lightbulb-outline</v-icon>
            &nbsp;{{ $t('Improve idea') }}
          </v-btn>
          <v-btn
            color="danger"
            class="no-text-transform"
            block
            outlined
            large
            @click="showImprovementForm('PROBLEM')"
          >
            <v-icon>mdi-chat-outline</v-icon>
            &nbsp;{{ $t('Report problem') }}
          </v-btn>
        </div>
      </v-card-text>
    </v-card>
    <v-dialog
      v-model="isImprovementFormShowing"
      fullscreen
      hide-overlay
      transition="dialog-bottom-transition"
    >
      <div class="d-overlay"></div>
      <div class="dialog-container">
        <improve-form
          @close="closeImprovementForm"
          :item="newImprovement"
          :mode="newImprovement?newImprovement.type:'IMPROVEMENT'"
          :project="project"
          :key="intent"
        />
      </div>
    </v-dialog>
    <div ref="bottom"></div>
  </div>
</template>
<script>
import ImproveForm from "./improve/Form";
import { IdeaIssue } from "@/models";
export default {
  props: {
    idea: {
      type: Object,
      required: true
    },
    expanded: {
      type: Boolean,
      required: false
    },
    project: {
      type: Object,
      required: true
    }
  },
  components: {
    "improve-form": ImproveForm
  },
  data: () => ({
    dataExpanded: false,
    improveForm: null,
    intent: null,
    isImprovementFormShowing: false,
    newImprovement: null
  }),
  mounted() {
    this.dataExpanded = this.expanded;
    if (this.expanded) {
      this.$el.scrollIntoView({ block: "center" });
    }
  },
  updated() {
    if (this.expanded) {
      this.$el.scrollIntoView({ block: "center" });
    }
  },
  methods: {
    toggle() {
      //this.expanded = !this.expanded;
      this.$emit("toggle", this.idea);
    },
    showImprovementForm(type = "IMPROVEMENT") {
      this.newImprovement = new IdeaIssue().deserialize(this.idea);
      this.newImprovement.type = type;
      this.newImprovement.parentId = this.idea.id;
      this.newImprovement.parentType = "idea";
      this.newImprovement.projectId = this.idea.projectId;
      this.newImprovement.processId = this.idea.idea.processId;
      this.newImprovement.stageId = this.idea.stageId;
      this.newImprovement.description = null;
      this.newImprovement.id = undefined;
      this.intent = new Date().getUTCMilliseconds();
      this.isImprovementFormShowing = true;
    },
    closeImprovementForm() {
      this.isImprovementFormShowing = false;
    }
  }
};
</script>